"use client"

import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Card, CardContent } from "@/components/ui/card"
import { useState } from "react"

export default function GercabApp() {
  const [absenList, setAbsenList] = useState<
    { nama: string; kelas: string; keterangan: string }[]
  >([])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const form = e.target as HTMLFormElement
    const nama = (form.elements.namedItem("nama") as HTMLInputElement).value
    const kelas = (form.elements.namedItem("kelas") as HTMLInputElement).value
    const keterangan = (form.elements.namedItem("keterangan") as HTMLInputElement).value

    if (nama && kelas && keterangan) {
      setAbsenList([...absenList, { nama, kelas, keterangan }])
      form.reset()
    }
  }

  return (
    <div className="p-4">
      <Tabs defaultValue="absen" className="w-full max-w-xl mx-auto">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="absen">Absen</TabsTrigger>
          <TabsTrigger value="daftar">Daftar</TabsTrigger>
        </TabsList>
        <TabsContent value="absen">
          <Card>
            <CardContent className="pt-6">
              <form className="space-y-4" onSubmit={handleSubmit}>
                <div className="grid gap-2">
                  <label htmlFor="nama">Nama</label>
                  <input
                    type="text"
                    name="nama"
                    id="nama"
                    className="border px-3 py-2 rounded-md"
                    placeholder="Masukkan nama"
                  />
                </div>
                <div className="grid gap-2">
                  <label htmlFor="kelas">Kelas</label>
                  <input
                    type="text"
                    name="kelas"
                    id="kelas"
                    className="border px-3 py-2 rounded-md"
                    placeholder="Masukkan kelas (misal: 7A)"
                  />
                </div>
                <div className="grid gap-2">
                  <label htmlFor="keterangan">Keterangan</label>
                  <input
                    type="text"
                    name="keterangan"
                    id="keterangan"
                    className="border px-3 py-2 rounded-md"
                    placeholder="Hadir / Izin / Sakit"
                  />
                </div>
                <button
                  type="submit"
                  className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md"
                >
                  Simpan Absen
                </button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>
        <TabsContent value="daftar">
          <Card>
            <CardContent className="pt-6 space-y-2">
              {absenList.length === 0 ? (
                <p>Belum ada data absen.</p>
              ) : (
                absenList.map((item, index) => (
                  <div key={index} className="border p-2 rounded-md">
                    <strong>{item.nama}</strong> - {item.kelas} ({item.keterangan})
                  </div>
                ))
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
